import Head from 'next/head';
import React,{Component} from 'react';
export default class Layout extends Component{
    render({children}){
        return (
            <div>
                <Head>
                    <title>高能组件哈哈哈</title>
                </Head>
                {children}
                <footer>版权所有请勿抄袭</footer>
            </div>
        )
    }
}